<template>
  <div id="taskAdd">
    <Modal
      v-model="taskBuild"
      title=""
      @on-cancel="reset"
    >
      <Form ref="taskAddForm" :model="taskAddData" :rules="taskAddRuleValidate" :label-width="85" style="padding:0 20%;">
        <FormItem prop="cl002" label="">
          <Input v-model.trim="taskAddData.cl002" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="cl004" label="">
          <i-select v-model.trim="taskAddData.cl004" style="width: 100%" placeholder="">
            <i-option value="0"></i-option>
            <i-option value="1"></i-option>
          </i-select>
        </FormItem>
        <FormItem prop="cl010" label="">
          <DatePicker size="default" :editable="false" type="year" v-model.trim="taskAddData.cl010" placement="bottom-end" placeholder="" style="width: 100%"></DatePicker>
        </FormItem>
        <FormItem prop="cl011" label="">
          <i-select v-model.trim="taskAddData.cl011" style="width: 100%" placeholder="">
            <i-option value="1"></i-option>
            <i-option value="2"></i-option>
          </i-select>
        </FormItem>
        <FormItem prop="ledgerIdList" label="">
          <i-select v-model.trim="taskAddData.ledgerIdList" filterable style="width: 100%" placeholder="">
            <i-option v-for="item in ledgerList" :value="item.value">{{ item.label }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="cl009" label="">
          <i-input v-model.trim="taskAddData.cl009" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder=""></i-input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="taskAddSubmit"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
// 
import { renderYearTime } from '@/utils/common'
import {
  // 
  getLedgerList,
  // 
  insertTask
} from '@/api/taskManager'
export default {
  name: 'TaskAdd',
  data () {
    return {
      /*  */
      ledgerList: [],
      /*  */
      taskAddData: {},
      /*  */
      taskAddRuleValidate: {
        cl002: [
          { required: true, message: '', trigger: 'blur' }
        ],
        ledgerIdList: [
          { required: true, message: '', trigger: 'change', type: 'number' }
        ],
        cl004: [
          { required: true, message: '', trigger: 'change', type: 'string' }
        ]
      },
      /*  */
      taskBuild: false
    }
  },
  mounted () {
    this.getLedgerList()
  },
  methods: {
    // 
    async getLedgerList () {
      await getLedgerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.ledgerList.push({ 'value': item.id, 'label': item.name })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    taskAddSubmit () {
      this.$refs.taskAddForm.validate(valid => {
        if (valid) {
          let arr = []
          arr.push(this.taskAddData.ledgerIdList)
          insertTask({
            data: {
              cl002: this.taskAddData.cl002,
              cl004: this.taskAddData.cl004,
              cl010: renderYearTime(this.taskAddData.cl010),
              ledgerIdList: arr,
              cl011: this.taskAddData.cl011,
              cl009: this.taskAddData.cl009
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '' })
              this.taskBuild = false
              this.taskAddData = {}
              this.reset()
              this.$emit('getTableData')
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch(() => {
            this.$Notice.error({ title: '' })
          })
        }
      })
    },
    // 
    reset () {
      this.taskAddData = {
        cl002: '',
        cl004: '',
        cl010: '',
        ledgerIdList: '',
        cl011: '',
        cl009: ''
      }
      this.$refs.taskAddForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
